import React, { memo } from "react";
import styled from "styled-components";
import { RoundImage, RoundImageProps } from "../../atoms/RoundImage/RoundImage";
import size from "../../../constants/size";

export interface ImgWIthCountDownProps {
  image: RoundImageProps["image"];
  ratio?: RoundImageProps["ratio"];
}

/**
 * Styled component.
 */
const Main = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: auto;
  height: 100%;
  text-align: center;
`;

/**
 * Functional component.
 * @param props 
 */
export const ImgWIthCountDown: React.FC<ImgWIthCountDownProps> = memo(({
  children,
  image,
  ratio
}) => {
  return (
    <>
      <RoundImage
        image={ image }
        ratio={ ratio ? ratio : size.illustRatio }
      >
        <Main>
          { children }
        </Main>
      </RoundImage>
    </>
  );
});