import React, { memo } from "react";
import { QuestionTitle } from "../../components/atoms/QuestionTitle/QuestionTitle";
import { StartButton } from "../../components/molecules/StartButton/StartButton";
import { Mb } from "../../components/atoms/Mb";
import { ResultBalloon } from "../../components/molecules/ResultBalloon/ResultBalloon";
import { QuestionResultText } from "../../components/atoms/QuestionResultText/QuestionResultText";
import { DoyaPenguin } from "../../components/molecules/DoyaPenguin/DoyaPenguin";
import Ruby from "../../components/atoms/Ruby/Ruby";
import useSetHeader from "../../hooks/useSetHeader";
import useSetQueryWithoutHistory from "../../hooks/useSetQueryWithoutHistory";

export interface MainProps {
  pageNum: number;
  setPageNum: React.Dispatch<React.SetStateAction<number>>;
}

/**
 * Functional component.
 * @param props 
 */
export const ResultFailed: React.FC<MainProps> = memo(({
  pageNum,
  setPageNum
}) => {

  useSetHeader({
    nextHref: "/menus/menu8/main",
    nextTransitionKey: "slideAsVerticalPrev",
    isFinish: true
  });
  useSetQueryWithoutHistory(pageNum);

  return (
    <>
      <Mb
        size={ {
          pc: 63,
          tb: 63,
          sp: 68
        } }
      >
        <QuestionTitle icon="quiz_icon.png">{ <><Ruby ruby="けっかはっぴょう">結果発表</Ruby>！</> }</QuestionTitle>
      </Mb>
      <Mb
        size={ {
          pc: 58,
          tb: 58,
          sp: 58
        } }
      >
        <QuestionResultText
          bottom={ <><Ruby ruby="じかん">時間</Ruby><Ruby ruby="ぎ">切</Ruby>れ</> }
        />
      </Mb>
      <Mb
        size={ {
          pc: 13,
          tb: 13,
          sp: 28
        } }
      >
        <ResultBalloon condition={ false } text={ <><Ruby ruby="きゅうじょしっぱい">救助失敗</Ruby>…</> }>
          <DoyaPenguin imgPath="chara_accuracyrate1.png" />
        </ResultBalloon>
      </Mb>
      <div onClick={ () => setPageNum(0) }>
        <StartButton>もう一度！</StartButton>
      </div>
    </>
  );
});
