import React, { memo } from "react";
import styled from "styled-components";
import { sourceHanSansJpBold } from "../../../styles/fonts";
import customMedia from "../../../styles/customMedia";
import DependsSize from "../../../Types/DependsSize";

export interface CSImageProps {
  desc: string;
  maxWidth?: DependsSize["size"];
}

/**
 * Styled component.
 */
const Main = styled.div<CSImageProps>`
  margin: 0 auto;
  max-width: ${ props => props.maxWidth ? props.maxWidth.pc + "px" : "100%" };
  ${ customMedia.lessThan<CSImageProps>("tb")`
    max-width: ${ props => props.maxWidth ? props.maxWidth.tb + "px" : "100%" };
  ` };
  ${ customMedia.lessThan<CSImageProps>("sp")`
    max-width: ${ props => props.maxWidth ? props.maxWidth.sp + "px" : "100%" };
  ` };
  &:after {
    content: "提供：${ props => props.desc }";
    width: 100%;
    display: block;
    text-align: right;
    ${ sourceHanSansJpBold }
    color: #1A1D4A;
    font-size: 18px;
    ${ customMedia.lessThan("sp")`
      font-size: 12px;
    ` };
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const CSImage: React.FC<CSImageProps> = memo(({
  children,
  desc,
  maxWidth
}) => {
  return (
    <>
      <Main desc={ desc } maxWidth={ maxWidth }>{ children }</Main>
    </>
  );
});