import React, { memo } from "react";
import styled from "styled-components";
import { heiseiMaruGothicStdW8, sourceHanSansJpBold } from "../../../styles/fonts";
import customMedia from "../../../styles/customMedia";

export interface ScoreBalloonProps {
  condition: boolean;
}

/**
 * Styled component.
 */
const goodColor = "#FFE898";
const badColor = "#AAEFEB";
const Main = styled.div<ScoreBalloonProps>`
  ${ heiseiMaruGothicStdW8 }
  color: ${ props => props.condition ? "#F34A00" : "#3946BF" };
  background-color: ${ props => props.condition ? goodColor : badColor };
  text-align: center;
  border-radius: 60px;
  padding: 70px 0 37px 0;
  position: relative;
  animation: bounds ease 1s;
  @keyframes bounds {
    0%{
      transform: scale(1.5);
    }
    20% {
      transform: scale(.8);
    }
    100% {
      transform: scale(1);
    }
  }
  font-size: 63px;
  ${ customMedia.lessThan("sp")`
    font-size: 54px;
  ` };
  &:before {
    content: "";
    position: absolute;
    bottom: -22px;
    left: 0;
    right: 0;
    width: 0;
    height: 0;
    margin: auto;
    border-style: solid;
    border-width: 22px 12.5px 0 12.5px;
    border-color: ${ props => props.condition ? goodColor : badColor } transparent transparent transparent;    
  }
  & > .point {
    font-size: 36px;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const ScoreBalloon: React.FC<ScoreBalloonProps> = memo(({
  children,
  condition
}) => {
  return (
    <>
      <Main condition={ condition }>{ children }</Main>
    </>
  );
});