
import React, { memo, useEffect } from "react";
import styled from "styled-components";
import { sidePadding, mainWrapper } from "../../../styles/wrapper";
import colors from "../../../constants/colors";
import { CreateBgColor } from "../../../containers";
import time from "../../../constants/time";
import size from "../../../constants/size";
import { shark, bottom } from "../../../styles/background";
import { HeaderOvr } from "../../../components/organisms/HeaderOvr/HeaderOvr";

export interface ContentsProps {
}

/**
 * Styled component.
 */
const Wrapper = styled.div`
  width: 100%;
  background-color: ${ colors.blue };
  &:before, &:after {
    content: "";
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
  }
  &:before {
    ${ shark }
  }
  &:after {
    background-repeat: no-repeat;
    ${ bottom }
  }
`;

const Main = styled.div`
  ${ sidePadding }
  padding-top: ${ size.headerOvrGap + size.controlUnitTopMargin + 20 }px;
  width: 100%;
  max-width: ${ size.contentMaxWidth }px;
  background-color: #fff;
  margin: 0 auto;
  min-height: 100vh;
  position: relative;
  padding-bottom: 100px;
  z-index: 1;
`;

const ChildWrapper = styled.div`
  ${ mainWrapper }
`;

/**
 * Functional component.
 * @param props 
 */
export const Content: React.FC<ContentsProps> = memo(({
  children
}) => {

  const { setBgColor } = CreateBgColor.useContainer();

  useEffect(() => {
    setTimeout(() => { setBgColor(colors.blue); }, time.transitionAnimation);
  }, []);

  return (
    <>
      <Wrapper>
        <Main>
          <HeaderOvr />
          <ChildWrapper className="transitionFade">
            { children }
          </ChildWrapper>
        </Main>
      </Wrapper>
    </>
  );
});
