import React, { memo } from "react";
import styled from "styled-components";
import { ScoreBalloon } from "../../atoms/ScoreBalloon/ScoreBalloon";

export interface ResultBalloonProps {
  condition: boolean;
  score?: number;
  text?: React.ReactElement<any, string | React.JSXElementConstructor<any>>;
}

/**
 * Styled component.
 */
const ScoreBalloonWrap = styled.div`
  width: 100%;
  max-width: 418px;
  margin: 0 auto 50px auto;
`;

/**
 * Functional component.
 * @param props 
 */
export const ResultBalloon: React.FC<ResultBalloonProps> = memo(({
  condition,
  score,
  text,
  children
}) => {
  return (
    <>
      <ScoreBalloonWrap>
        <ScoreBalloon condition={ condition }>
          { score && <>{ score }<span className="point">点</span></> }
          { text && <>{ text }</> }
        </ScoreBalloon>
      </ScoreBalloonWrap>
      { children }
    </>
  );
});
