import React, { memo } from "react";
import { QuestionTitle } from "../../components/atoms/QuestionTitle/QuestionTitle";
import { Mb } from "../../components/atoms/Mb";
import { ResultBalloon } from "../../components/molecules/ResultBalloon/ResultBalloon";
import { DoyaPenguin } from "../../components/molecules/DoyaPenguin/DoyaPenguin";
import Ruby from "../../components/atoms/Ruby/Ruby";
import useSetHeader from "../../hooks/useSetHeader";
import useSetQueryWithoutHistory from "../../hooks/useSetQueryWithoutHistory";

export interface MainProps {
  pageNum: number;
}

/**
 * Functional component.
 * @param props 
 */
export const ResultClear: React.FC<MainProps> = memo(({
  pageNum
}) => {

  useSetHeader({
    nextHref: "/menus/menu8/main",
    nextTransitionKey: "slideAsVerticalPrev",
    isFinish: true
  });
  useSetQueryWithoutHistory(pageNum);

  return (
    <>
      <Mb
        size={ {
          pc: 157,
          tb: 157,
          sp: 162
        } }
      >
        <QuestionTitle icon="quiz_icon.png">{ <><Ruby ruby="けっかはっぴょう">結果発表</Ruby>！</> }</QuestionTitle>
      </Mb>
      <ResultBalloon condition={ true } text={ <><Ruby ruby="きゅうじょせいこう">救助成功</Ruby>！</> }>
        <DoyaPenguin imgPath="chara_accuracyrate2.png" />
      </ResultBalloon>
    </>
  );
});