
import React, { memo } from "react";
import styled from "styled-components";
import { heiseiMaruGothicStdW8 } from "../../../styles/fonts";

export interface QuestionResultTextProps {
  top?: React.ReactElement<any, string | React.JSXElementConstructor<any>>
  bottom?: React.ReactElement<any, string | React.JSXElementConstructor<any>>
};

/**
 * Styled component.
 */
const Main = styled.div`
  width: 100%;
  ${ heiseiMaruGothicStdW8 }
  color: #F53636;
  text-align: center;
  & > .top {
    font-size: 18px;
    margin-bottom: 26px;
  }
  & > .bottom {
    font-size: 36px;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const QuestionResultText: React.FC<QuestionResultTextProps> = memo(({
  top,
  bottom
}) => {
  return (
    <>
      <Main>
        <div className="top">{ top }</div>
        <div className="bottom">{ bottom }</div>
      </Main>
    </>
  );
});
