import React, { memo } from "react";
import styled from "styled-components";
import { sourceHanSansJpBold } from "../../../styles/fonts";
import customMedia from "../../../styles/customMedia";
import DependsSize from "../../../Types/DependsSize";

export interface MainTextSourceHanSansJpProps {
  fontSize: DependsSize["size"];
}

/**
 * Styled component.
 */
const Main = styled.div<MainTextSourceHanSansJpProps>`
  color: #1A1D4A;
  ${ sourceHanSansJpBold }
  line-height: 1.7;
  ${ customMedia.greaterThan<MainTextSourceHanSansJpProps>("tb")`
    font-size: ${ props => props.fontSize.pc }px;
  ` };
  ${ customMedia.lessThan<MainTextSourceHanSansJpProps>("tb")`
    font-size: ${ props => props.fontSize.tb }px;
  ` };
  ${ customMedia.lessThan<MainTextSourceHanSansJpProps>("sp")`
    font-size: ${ props => props.fontSize.sp }px;
  ` };
`;

/**
 * Functional component.
 * @param props 
 */
export const MainTextSourceHanSansJp: React.FC<MainTextSourceHanSansJpProps> = memo(({
  children,
  fontSize
}) => {
  return (
    <>
      <Main fontSize={ fontSize }>{ children }</Main>
    </>
  );
});