import { useEffect, useState, Dispatch, SetStateAction } from "react";
import useInterval from "use-interval";

const useCallRescue = (
  setPageNum: Dispatch<SetStateAction<number>>
): number => {
  const countMax = 5;
  // Count down.
  const [count, setCount] = useState(countMax);
  useInterval(() => setCount(state => state - 1), 1000);

  // Move to next page.
  useEffect(() => {
    const timer = setTimeout(() => setPageNum(state => state + 1), 5000);
    return () => clearTimeout(timer);
  }, []);

  return count;
}

export default useCallRescue;
