import React, { memo } from "react";
import styled from "styled-components";
import { PenguinAnimBase } from "../../atoms/PenguinAnimBase/PenguinAnimBase";
import customMedia from "../../../styles/customMedia";

export interface DoyaPenguinProps {
  imgPath: string;
}

/**
 * Styled component.
 */
const Main = styled.div`
  width: 100%;
  max-width: 334px;
  margin: 0 auto;
  ${ customMedia.lessThan("tb")`
    width: 244px;
  ` };
`;

/**
 * Functional component.
 * @param props 
 */
export const DoyaPenguin: React.FC<DoyaPenguinProps> = memo(({
  imgPath
}) => {
  return (
    <>
      <Main>
        <PenguinAnimBase imageUrl={ imgPath } />
      </Main>
    </>
  );
});