import React, { memo } from "react";
import styled from "styled-components";
import DependsSize from "../../Types/DependsSize";
import customMedia from "../../styles/customMedia";

export interface MbProps extends DependsSize {
}

/**
 * Styled component.
 */
const Main = styled.div<DependsSize>`
  margin-bottom: ${ props => props.size.pc }px;
  ${ customMedia.lessThan<DependsSize>("tb")`
    margin-bottom: ${ props => props.size.tb }px;
  ` };
  ${ customMedia.lessThan<DependsSize>("sp")`
    margin-bottom: ${ props => props.size.sp }px;
  ` };
`;

/**
 * Functional component.
 * @param props 
 */
export const Mb: React.FC<MbProps> = memo(({
  size,
  children
}) => {
  return (
    <>
      <Main size={ size }>{ children }</Main>
    </>
  );
});