import React, { memo } from "react";
import styled from "styled-components";

export interface PenguinAnimBaseProps {
  imageUrl: string;
}

/**
 * Styled component.
 */
const Main = styled.div.attrs<PenguinAnimBaseProps>(
  (props) => ({
    style: {
      backgroundImage: `url(/${ props.imageUrl })`
    }
  })
)<PenguinAnimBaseProps>`
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  &:before {
    content: "";
    display: block;
    padding-top: 93.433%;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const PenguinAnimBase: React.FC<PenguinAnimBaseProps> = memo(({
  imageUrl
}) => {
  return (
    <>
      <Main imageUrl={ imageUrl } />
    </>
  );
});
