import { css } from "styled-components";
import fontFamilies from "../constants/fontFamilies";

/**
 * 平成丸ゴシック Std
 */
const heiseiMaruGothicStd = css`
  font-family: ${ fontFamilies.heiseiMaruGothicStd };
  font-style: normal;
`;

export const heiseiMaruGothicStdW4 = css`
  ${ heiseiMaruGothicStd }
  font-weight: 400;
`;

export const heiseiMaruGothicStdW8 = css`
  ${ heiseiMaruGothicStd }
  font-weight: 800;
`;

