import React, { memo } from "react";
import styled from "styled-components";

export interface RoundImageProps {
  image: string;
  ratio: number;
  transImage?: boolean;
}

/**
 * Styled component.
 */
const Main = styled.div<RoundImageProps>`
  width: 100%;
  background-image: ${ props => props.transImage ?  `url(/${ props.image })` : "none" };
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-radius: 10px;
  position: relative;
  &:before {
    content: "";
    display: block;
    padding-top: ${ props => props.ratio }%;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const RoundImage: React.FC<RoundImageProps> = memo(({
  image,
  ratio,
  transImage,
  children
}) => {

  const isOpacity = transImage === void(0) ? true : transImage;

  return (
    <>
      <Main image={ image } ratio={ ratio } transImage={ isOpacity }>{ children }</Main>
    </>
  );
});
