import { useEffect } from "react";
import { useRouter } from "next/router";
import TransTimeType from "../Types/TransTimeType";
import { TransitionKeyContainer } from "../containers";
import time from "../constants/time";

const useInitTransition = (setTransTime: TransTimeType) => {
  const router = useRouter();
  const { setTransitionKey } = TransitionKeyContainer.useContainer();

  useEffect(() => {
    router.beforePopState((_) => {
      setTransTime(0);
      setTransitionKey("init");
      return true;
    });
  });

  useEffect(() => {
    setTransTime(time.transitionAnimation);
  }, []);
}

export default useInitTransition;
