import { useState } from "react";
import { LightHouseButtonProps } from "../components/atoms/LightHouseButton/LightHouseButton";
import { PageProgressProps } from "../components/atoms/PageProgress/PageProgress";
import { TransProps } from "../components/atoms/Trans";

export interface useControlButtonsProps {
  color?: LightHouseButtonProps["color"];
  isFinish?: boolean;
  prevHref?: string;
  nextHref?: string;
  prevClick?: () => void;
  nextClick?: () => void;
  prevTransitionKey?: TransProps["transitionKey"];
  nextTransitionKey?: TransProps["transitionKey"];
  pageProgress?: {
    currentPage: PageProgressProps["currentPage"];
    pageAmount: PageProgressProps["pageAmount"];
  };
}

export default () => {
  const [visibleControlButtons, setVisibleControlButtons]
    = useState<useControlButtonsProps>({
      color: void(0),
      isFinish: void(0),
      prevHref: void(0),
      nextHref: void(0),
      prevClick: void(0),
      nextClick: void(0),
      prevTransitionKey: "init",
      nextTransitionKey: "init",
      pageProgress: void(0)
    });
  return {
    visibleControlButtons, setVisibleControlButtons
  };
}
