
import React, { memo, useState, useEffect } from "react";
import time from "../../constants/time";
import styled from "styled-components";

export interface OpacityEffectProps {
  visible: boolean;
  isAbort?: boolean;
  isEnteredDelay?: boolean;
}

/**
 * Styled component.
 */
interface IMainProps {
  visible: OpacityEffectProps["visible"];
  transition: boolean;
}
const Main = styled.div.attrs<IMainProps>(
  (props) => ({
    style: {
      opacity: props.visible ? 1 : 0,
      transition: props.transition ? `opacity ${ time.objectEffect }ms ease` : "none"
    }
  })
)<IMainProps>`
`;

/**
 * The Functional component for page move as OpacityEffectition animation.
 * @param props 
 */
export const OpacityEffect: React.FC<OpacityEffectProps> = memo(({
  visible,
  isAbort,
  isEnteredDelay,
  children
}) => {

  const [exist, setExist] = useState(false);
  const [opacity, setOpacity] = useState(false);
  useEffect(() => {
    if (visible) {
      if (isAbort) {
        setOpacity(visible);
        setExist(visible);
      } else {
        const timer = setTimeout(() => {
          setOpacity(visible);
        }, isEnteredDelay ? time.transitionAnimation : 0);
        setExist(visible);
        return () => {
          clearTimeout(timer);
        }
      }
    } else {
      setOpacity(visible)
      const timer = setTimeout(() => {
        setExist(visible);
      }, time.transitionAnimation);
      return () => {
        clearTimeout(timer);
      }
    }
  }, [visible]);

  // Add transition.
  const [transition, setTransition] = useState(false);
  useEffect(() => {
    setTimeout(() => setTransition(true), time.transitionAnimation);
  }, []);

  return (
    <>
      {
        exist && <Main visible={ opacity } transition={ transition }>{ children }</Main>
      }
    </>
  );
});
