import { useState } from "react";
 
export interface useWaveStateProps {
  initPos?: string;
  loopAnimatePos?: number;
}

export default () => {

  const [initPos, setInitPos] = useState<useWaveStateProps["initPos"]>();
  const [loopAnimatePos, setLoopAnimatePos] = useState<useWaveStateProps["loopAnimatePos"]>(0);

  return {
    initPos, setInitPos,
    loopAnimatePos, setLoopAnimatePos
  };
}
