import React, { Dispatch, useState, SetStateAction, useEffect } from "react";
import { OpacityEffect } from "../components/atoms/OpacityEffect";

/**
 * useSwitchPage
 * @param contents 
 * @returns 
 */
const useSwitchPage = (contents: (setPageNum: Dispatch<SetStateAction<number>>, pageNum: number) => React.ReactElement[]) => {
  const [pageNum, setPageNum] = useState(0);
  const [pages, setPages] = useState([]);

  useEffect(() => {
    window.scrollTo({ top: 0 });

    setPages(
      contents(setPageNum, pageNum).map((x, num) => {
        return (
          <div key={ num }>
            <OpacityEffect visible={ num === pageNum } isAbort={ num === 0 } isEnteredDelay={ true }>
              { x }
            </OpacityEffect>
          </div>
        );
      })
    )
  }, [pageNum]);

  return pages;
}

export default useSwitchPage;
