import { css } from "styled-components";

export const brightness = css`
  transition: filter .2s ease;
  &:hover {
    filter: brightness(130%);
  }
`;

export const opacity = css`
  transition: opacity .2s ease;
  &:hover {
    opacity: .65;
  }
`;

export const whiteness = css`
  & > * {
    transition: opacity .2s ease;
  }
  &:hover {
    & > * {
      opacity: .65;
    }
  }
`;
