import React, { memo } from "react";
import styled from "styled-components";
import { heiseiMaruGothicStdW8 } from "../../../styles/fonts";
import customMedia from "../../../styles/customMedia";

export interface QuestionTitleProps {
  icon: string;
}

/**
 * Styled component.
 */
const color = "#1A1D4A";
const circleSize = 60;
const Main = styled.div<QuestionTitleProps>`
  position: relative;
  ${ heiseiMaruGothicStdW8 }
  color: ${ color };
  line-height: 1.5;
  font-size: 36px;
  padding-left: ${ circleSize + 10 }px;
  ${ customMedia.lessThan("sp")`
    font-size: 28px;
    padding-left: 0;
    text-align: center;
  ` };
  &:before {
    content: "";
    color: #fff;
    background-image: url("/${ props => props.icon }");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: ${ circleSize }px;
    height: ${ circleSize }px;
    position: absolute;
    text-align: center;
    line-height: ${ circleSize }px;
    top: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    ${ customMedia.lessThan("sp")`
      display: none;
    ` };
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const QuestionTitle: React.FC<QuestionTitleProps> = memo(({
  children,
  icon
}) => {
  return (
    <>
      <Main icon={ icon }>{ children }</Main>
    </>
  );
});