import React, { memo, useEffect, useState } from "react";
import styled from "styled-components";
import size from "../../../constants/size";
import { ControlButtons } from "../../../containers";

export interface HeaderOvrProps {
}

/**
 * Styled component.
 */
const Main = styled.div`
  position: fixed;
  width: 100%;
  max-width: ${ size.contentMaxWidth }px;
  padding-top: ${ size.headerOvrGap + size.controlUnitTopMargin }px;
  background-color: #fff;
  z-index: 4;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
`;

/**
 * Functional component.
 * @param props 
 */
export const HeaderOvr: React.FC<HeaderOvrProps> = memo(({
}) => {

  const [visible, setVisible] = useState(true);
  const { visibleControlButtons } = ControlButtons.useContainer();

  useEffect(() => {
    if (!visibleControlButtons.color &&  !visibleControlButtons.nextHref && !visibleControlButtons.prevHref) {
      setVisible(false);
    } else {
      setVisible(true);
    }
  }, [visibleControlButtons]);

  return visible ? (
    <>
      <Main />
    </>
  ) : null;
});